@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400&display=swap");


* {
  padding: 0;
  margin: 0;
  box-sizing: border-box !important;
}

:root {
  /* colors */
  --primary-100: #e1bee7;
  --primary-200: #ce93d8;
  --primary-300: #ab47bc;
  --primary-400: #8e24aa;
  --primary-500: #7b1fa2;
  --primary-600: #6a1b9a;
  --primary-700: #4a148c;

  
  /* grey */
  --grey-50: #f8fafc;
  --grey-100: #f1f5f9;
  --grey-200: #e2e8f0;
  --grey-300: #cbd5e1;
  --grey-400: #94a3b8;
  --grey-500: #64748b;
  --grey-600: #475569;
  --grey-700: #334155;
  --grey-800: #1e293b;
  --grey-900: #0f172a;

  /* rest of the colors */
  --black: #222;
  --white: #fff;
  --red-light: #f8d7da;
  --red-dark: #de354c;
  --green-light: #d1e7dd;
  --green-dark: #03a032;

  /* fonts  */
  --headingFont: "Lato", sans-serif;
  --bodyFont: "Lato", sans-serif;
  --smallText: 0.7em;
  /* rest of the vars */
  --backgroundColor: var(--grey-100);
  --textColor: var(--grey-900);
  --borderRadius: 0.25rem;
  --letterSpacing: 1px;
  --transition: 0.3s ease-in-out all;
  --max-width: 1120px;
  --fixed-width: 600px;
  --nav-width: 200px;

  /* box shadow*/
  --shadow-1: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  --shadow-2: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  --shadow-3: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  --shadow-4: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  --shadow-5: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
  --shadow-6: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset,
    rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

body {
  background: var(--backgroundColor);
  font-family: var(--bodyFont);
  font-weight: 400;
  font-size: 0.8rem;
  color: var(--textColor);
}


.app-header{
    background-color: var(--primary-700);
    padding: 1rem 1.5rem;
    transition: margin-left 0.5s ease-in-out;
    position: fixed;
    top: 0;
    width: 100%;
    z-index:9999;;

}



/* ACTUALS HEADER*/
.search-bar{
    background-color: white;
    border-radius: 0.3rem;
    width: 350px;
    padding: 0.2rem 0.5rem;
}

.search-bar input{
    background-color: transparent;
    padding: 0.6rem;
    flex: 4;
}


.search-bar i {
    color: #ccc;
    cursor: pointer;
    padding: 0.6rem;
    flex: 0.5;
    font-size: .9rem;
    text-align: center;
}

.search-bar i:hover{
    color: var(--primary-600);
}

.app-header .toogle-bar{
    font-size: 1.3rem;
    cursor: pointer;
    color: white;
    width: 50px;
}

/* .right-nav-panel{
    background-color: red;
} */
.right-nav-panel .panel-icon{
    color: white;
    margin: 0 0.6rem;
    padding: 0 0.6rem;
    font-size: 1.1rem;
    cursor: pointer;
}

.panel-icon:hover{
    color: var(--primary-600)
}

.avatar{
    border-radius:50%;
    background: white;
    padding: 0.4rem 0.3rem;
    margin: 0 0.6rem;
    cursor: pointer;
}

.avatar img{
    object-fit: cover;
    width: 50px;
    height: 50px;
}

/* .nav-alert{
    
} */


.nav-alert-box{
    position: absolute;
    background-color: white;
    width: 180px;
    /* padding: 0.8rem; */
    /* margin-top: 1rem; */
    right: 20px;
    border-radius: 10px;
    box-shadow: var(--shadow-2);
    text-align: center;
}

.user-names{
    /* margin-bottom: 0.6rem; */
    font-size: 0.65rem;
    color: var(--grey-600);
    text-align: center;
    padding: 0.8rem;
    line-height: 1.4;
}

.user-names p{
    color: var(--grey-500);
}




.sign-out{
    color: var(--primary-700);
    margin-right: 10px;
}
.nav-alert-box h4{
    text-align: center;
}

.sign{
    padding: 1rem;
    border-top: 1px solid var(--grey-200);
    cursor: pointer;
}
.sign:hover{
   background-color: var(--primary-100);
   color: var(--primary-700)
}

.nav-alert-info{
    position: absolute;
    background-color: white;
    width: 300px;
    border-radius: 10px;
    box-shadow: var(--shadow-2);
}

.info-h{
    padding: 1rem;
}

.info-card{
    padding: 1.5rem;
    border-top: 1px solid var(--grey-300);
}

.info-card:hover{
    background-color: var(--primary-100);
    color: var(--primary-700);
    cursor: pointer;
}

.barge-h{
    position: absolute;
    width: 5;
    height: 5;
    border-radius: 50%;
    background-color: red;
}



/* ACTUAL HEADER CLOSED */

/* UTILITIES */
input{
    outline: none;
    border: none;
}
.flex-middle{
    display: flex;
    justify-content: center;
    align-items: center;
}
.flex-side{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.flex-even{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.flex-start{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.flex-end{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.arrow-up {
    width: 0; 
    height: 0; 
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-bottom: 12px solid black;
  }
  
  .arrow-down {
    width: 0; 
    height: 0; 
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    
    border-top: 20px solid #f00;
  }
  
  .arrow-right {
    width: 0; 
    height: 0; 
    border-top: 60px solid transparent;
    border-bottom: 60px solid transparent;
    
    border-left: 60px solid green;
  }
  
  .arrow-left {
    width: 0; 
    height: 0; 
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent; 
    
    border-right:10px solid blue; 
  }

  .blink {
    color: #333;
    animation: so-current 0.7s ease infinite;
    padding-left: 0.5rem;
  }
  
  @keyframes so-current {
    from {
      color: #333;
    }
    to {
      color: red;
    }
  }

  .ion-icon{
    color: var(--grey-700);
    font-size: .9rem;
    cursor: pointer;
    padding: 1rem;
  }

  .ion-icon i{
      margin-right: 0.95rem;
      font-size: 1rem;
      color: var(--primary-700);
  }


  .ion-icon:hover{
    color: var(--primary-700);
  }

  .app-hover-left{
      margin-left: 70px;
  }

  .app-hover-right{
      margin-left: 270px;
  }

  .app-hover-left:hover{
      transition: margin-left 0.2s ease-in-out;
  }

  .app-hover-right:hover{
      transition: margin-left 0.2s ease-in-out;
  }

  .mt-05{ margin-top: 0.5rem ;}
  .mt-1{ margin-top: 1rem ;}


  .grid-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
    grid-gap: 20px;
  }
  
  .grid-3 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: space-around;
    align-items: center;
    grid-gap: 0 1rem;
  }

  /* BTN */
  .btx {
    cursor: pointer;
    color: var(--white);
    background: var(--primary-500);
    border: transparent;
    border-radius: var(--borderRadius);
    letter-spacing: var(--letterSpacing);
    padding: 0.375rem 0.75rem;
    box-shadow: var(--shadow-1);
    transition: var(--transtion);
    text-transform: capitalize;
    display: inline-block;
  }
  
  .btx:hover{
    filter: brightness(140%);
  }

  /* UTILITIES END */


/* SIDE-BAR */
.side-main{
    background-color: white;
    position: fixed;
    top: 0;
    width: 22%;
    transition: width 0.5s ease-in-out;
    box-shadow: var(--shadow-1);
    height: 100vh;
    z-index: 99999;
}

.side-header{
    padding: 1.11rem;
    border-bottom: 1px solid var(--grey-200);
}

.side-icons{
    padding: 0 1.2rem;
}


/* SIDE-END */


