.card-self{
    background-repeat: no-repeat;
    background-position: right center;
    border-radius: .5rem;
    padding: 1rem;
    box-shadow: var(--shadow-2);
    width: 100%;
}

.card-self-img{
   height: 15rem;
}


/* CARD-SELF-END */



