    border-radius: 2px;
    background-color: white;
    padding: 0.9rem 1.8rem;
    text-overflow: ellipsis;
    color: #333;
    outline: none;
    margin: 10px 0;
    box-shadow: var(--shadow-1);
  }
  .bxn:hover{
    background-color: #f2f2f2;
  }
  
  .lines:hover{
    background: #f2f2f2;
    font-weight: 600;
  }
  
  
