   */
  .deactive {
    pointer-events: none;
    width: 60px;
    padding: 0.8rem 0.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    color: var(--primary-700);
  }
  
  .disabled {
    pointer-events: none;
    color: var(--grey-500);
  }
  
  .middle {
    display: grid;
    justify-content: center;
    align-items: center;
    height: 80vh;
    padding: 0 6rem;
    grid-template-columns: 1fr;
  }
  
  .absolutes {
    position: fixed;
    width: 100vw;
    background-color: rgba(139, 139, 139, 0.3);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000000;
    top: 0;
    left: 0;
  }
  .absolutes-dark {
    position: fixed;
    width: 100vw;
    background-color: rgba(139, 139, 139, 1);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000000;
    top: 0;
    left: 0;
  }
  
  .spinner {
    box-sizing: border-box;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    border: 2px solid transparent;
    border-top-color: var(--primary-700);
    border-bottom-color: var(--yellow-500);
    animation: spinner 0.7s ease infinite;
  }
  
  @keyframes spinner {
    to {
      transform: rotate(360deg);
    }
  }
  
  .white-roll {
    box-sizing: border-box;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    border: 2px solid transparent;
    border-top-color: var(--primary-100);
    border-bottom-color: var(--primary-200);
    animation: white-roll 0.7s ease infinite;
  }
  
  @keyframes white-roll {
    to {
      transform: rotate(360deg);
    }
  }
  
  .cancel {
    cursor: pointer;
    position: relative;
    right: 0;
    padding: 0.5rem 1rem;
    color: white;
    background-color: var(--red-dark);
    transition: background 0.3s;
  }
  
  .cancel:hover {
    filter: brightness(150%);
  }
  
  .fle:hover {
    background-color: var(--primary-700);
    color: white !important;
    cursor: pointer;
  }
  
  .input-ads {
    display: flex;
    width: 100%;
    justify-content: center;
    overflow: hidden;
    margin-top: 10px;
    line-height: 1.75;
    height: 50px;
    border-bottom: 1px solid var(--primary-700);
    background-color: white;
  }
  
  .input-txt {
    padding: 1.2rem 0.3rem;
    width: 100%;
    outline: none;
    border: none;
    font-size: 14px;
    position: relative;
    background-color: transparent;
    /* background-color: burlywood; */
    z-index: 100;
  }
  
  .input-icon {
    flex: 0.3;
    padding: 0.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    font-size: 1rem;
    margin-left: 5px;
  }
  
  #label {
    position: absolute;
    margin-top: 10px;
    transition: margin-top 0.2s ease-out;
    padding: 0 0.3rem;
  }
  
  .hoves:hover {
    background-color: var(--grey-50);
  }
  
  /* TABLES */
  .tables,
  .tab {
    padding: 10;
    border-collapse: collapse;
    text-align: left;
    background-color: white;
    font-size: 0.8rem;
    width: 100%;
    box-shadow: var(--shadow-1);
  }
  .tables tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  .tables tr th {
    border-bottom: solid 2px #ccc;
    font-weight: 900;
    padding: 1rem;
  }
  
  .tables tr td,
  .tab tr td {
    padding: 0.8rem;
    /* border-bottom: solid 1px #ccc; */
  }
  
  .tab tr th {
    border-bottom: solid 2px #ccc;
    padding: 0.7rem;
    text-align: left;
  }
  .tab tr td {
    padding: 0.3rem 0.7rem;
    text-align: left;
  }
  
  .tr td,
  .tr th {
    text-align: center;
  }
  .trL td,
  .trL th {
    text-align: left;
  }
  
  .pagi {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--primary-500);
    background-color: white;
    padding: 0.5rem;
  }
  
  .disabled {
    pointer-events: none;
    color: var(--grey-500);
  }
  
  .appear {
    background-color: white;
    /* padding: 1rem 1.5rem; */
    animation-name: example;
    animation-duration: 0.4s;
    padding-bottom: 1rem;
  }
  
  @keyframes example {
    0% {
      margin-top: -40px;
      opacity: 0;
    }
    50% {
      opacity: 0.5;
    }
    100% {
      background-color: 0;
      opacity: 1;
    }
  }
  
  .disappear {
    background-color: white;
    /* padding: 1rem 1.5rem; */
    animation-name: example1;
    animation-duration: 0.8s;
    padding-bottom: 1rem;
  }
  
  @keyframes example1 {
    0% {
      margin-top: 40px;
      opacity: 0;
    }
    50% {
      opacity: 0.5;
    }
    100% {
      background-color: 0;
      opacity: 1;
    }
  }
  .li-show {
    background-color: white;
    top: 200;
    position: absolute;
    animation-name: example2;
    animation-duration: 0.4s;
    padding-bottom: 1rem;
  }
  
  @keyframes example2 {
    0% {
      top: 0;
      opacity: 0;
    }
    50% {
      top: 2rem;
      opacity: 0.5;
    }
    100% {
      top: 2.5rem;
      opacity: 1;
    }
  }
  
  .lert {
    background-color: white;
    /* padding: 1rem 1.5rem; */
    animation-name: ert;
    animation-duration: 0.4s;
    padding-bottom: 1rem;
    width: 350px;
    z-index: 100;
    right: 0;
  }
  
  @keyframes ert {
    0% {
      right: -5px;
      opacity: 0;
    }
    50% {
      right: 0;
      opacity: 0.5;
    }
    100% {
      right: 20px;
      opacity: 1;
    }
  }
  
  /* SIDE */
  .side-parent {
    width: 4rem;
    background-color: var(--primary-600);
    color: var(--primary-300);
    height: 100vh;
    transition: width 0.5s;
    padding: 20;
  }
  
  .side-shrink {
    width: 300px;
    background-color: var(--primary-600);
    color: var(--primary-300);
    transition: width 0.5s;
    padding: 20;
    height: 100%;
  }
  
  /* HEADER */
  .header-back {
    width: 100%;
    padding: 0.5rem;
    height: 3rem;
    background-color: white;
    /* border-bottom: 1px solid #ccc; */
    /* box-shadow: var(--shadow-1); */
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  
  .icon-space {
    margin: 0 0.5rem;
    padding: 1rem;
    cursor: pointer;
    font-size: 0.9rem;
  }
  
  .icon-space-active {
    background-color: var(--primary-700);
    color: var(--primary-200);
  }
  
  .icon-space:hover {
    background-color: var(--primary-700);
    color: var(--primary-200);
  }
  
  .search-design {
    background-color: var(--primary-500);
    color: var(--primary-100);
    padding: 0.5rem;
    border: 1px solid var(--primary-700);
    border-radius: 5px;
  }
  



  @media screen and (max-width: 768px){
    .login{
      width: 80% !important;
    }
    .grid-3{
      grid-template-columns: 1fr;
      gap: 20px;
    }

    .side-main,
    .please-hide,
    .search-bar{
      display: none !important;
    }
    .body-main{
      padding: 1rem !important;
    }
    /* .app-header{
      padding: 1.5rem !important;
    } */

    .app-child-main{
      left: 2% !important;
    }


    
  }